@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

* {
  line-height: 120%;
}

.container {
  max-width: 1120px;
  padding: 0 10px;
  margin: 0 auto;
}

.header {
  position: relative;
  width: 100%;
  margin: 0 auto 25px auto;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 5px solid #dfdfdf;
  background: url(../img/bg.png);
  .burger-btn {
    z-index: 10;
    position: absolute;
    width: 40px;
    height: 30px;
    top: 25px;
    left: 20px;
    cursor: pointer;
    > span {
      position: absolute;
      background-color: $black-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 14px;
      @include transition;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $black-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }
    &:before {
      top: 27px;
    }
    &:after {
      bottom: 27px;
    }
    &_active {
      span {
        opacity: 0;
      }
      &:before {
        transform: rotate(45deg);
        top: 13px;
      }
      &:after {
        transform: rotate(-45deg);
        bottom: 14px;
      }
    }
  }
  &__menu{
    display: flex;
    flex-direction: column;
    z-index: 8;
    position: absolute;
    width: 100%;
    left: 0;
    top: -300px;
    background-color: $grey;
    background-image: url(../img/header__img.png);
    background-position-x: right;
    background-position-y: center;
    background-repeat: no-repeat;
    padding: 66px 0 35px 23px;
    @include transition;
    &_active{
      top: 0;
    }
    a{
      max-width: fit-content;
      font-family: $font-family-text;
      font-size: $title-fz-mobile;
      font-weight: 600;
      background: $black-color;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin-bottom: 15px;
      @include transition;
      &:last-child{
        margin-bottom: 0;
      }
      &:hover{
        background-image: linear-gradient(90deg, #E3535D, #2AA5A0);
      }
    }
  }
  &__logo {
    display: flex;
    max-width: 180px;
    width: 100%;
    margin: 0 auto;
    img{
      margin-right: 15px;
    }
    p {
      font-family: $font-family-heading;
      font-size: $logo-fz;
      font-weight: 600;
      color: $black-color;
      b{
        font-weight: 800;
      }
    }
  }
}

.news {
  width: 100%;
  padding: 12px 15px 17px 15px;
  margin-bottom: 25px;
  background: $contrast-color;
  box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  &:last-child {
    margin-bottom: 35px;
  }
  &__title{
    font-family: $font-family-heading;
    font-size: $title-fz;
    font-weight: 600;
    color: $black-color;
    margin-bottom: 5px;
  }
  &__text{
    font-family: $font-family-text;
    font-size: $normal-fz;
    font-weight: 400;
    color: $black-color;
    margin-bottom: 10px;
  }
  &__link{
    display: block;
    font-family: $font-family-text;
    font-size: $normal-fz;
    font-weight: 400;
    color: $primary-color-light;
  }
  &__sliders{
    margin-bottom: 25px;
  }
}

.popup-slider {
  width: 100%;
  margin-bottom: 10px;
  &__prev,
  &__next {
    position: absolute;
    top: 110px;
    z-index: 5;
    opacity: 0.8;
    cursor: pointer;
  }
  &__prev {
    left: 20px;
  }
  &__next {
    right: 20px;
  }
  &__slide {
    height: 240px !important;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  @media screen and (min-width:769px) {
    &__slide{
      img{
        object-fit: contain;
      }
    }
  }
}
.popup-thumbs {
  height: 50px;
  box-sizing: border-box;
  padding: 10px 0;
  width: 100%;
  .swiper-slide {
    height: 100%;
    max-width: 90px;
    padding: 1.5px;
    opacity: 0.5;
  }
  .swiper-slide-thumb-active {
    opacity: 1;
    border: 2px solid $primary-color;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.write-us, .our-contact{
  display: block;
  margin: 0 auto 17px auto;
  max-width: 250px;
  font-family: $font-family-text;
  font-size: $title-fz;
  font-weight: 400;
  color: $secondary-color;
  padding: 7px 40px;
  border: 2px solid $secondary-color;
  border-radius: 100px;
}

.our-contact{
  color: $primary-color;
  border-color: $primary-color;
  padding: 7px 30px;
  margin-bottom: 90px;
}

.footer{
  position: relative;
  padding: 20px 0 40px 0;
  background-color: $footer-color;
  background-image: url(../img/footer__img.png);
  background-position-x: left;
  background-position-y: center;
  background-repeat: no-repeat;
  &__wrapper{
    max-width: 1120px;
    padding: 0 20px;
    margin: 0 auto;
  }
  &__text{
    font-family: $font-family-text;
    font-weight: 400;
    font-size: $normal-fz;
    color: $footer-text-color;
    margin-bottom: 25px;
    b{
      font-weight: 800;
    }
  }
  &__line{
    width: 100%;
    height: 2px;
    background: $footer-text-color;
    margin-bottom: 20px;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
      font-weight: 600;
      margin-bottom: 0;
    }
  }
  &__socials{
    max-height: 25px;
    width: 205px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__link{
    width: 25px;
    height: 25px;
  }
  &__telegram{
    background: url(../img/footer__telegram.png) center center no-repeat;
  }
  &__facebook{
    background: url(../img/footer__facebook.png) center center no-repeat;
  }
  &__inst{
    background: url(../img/footer__inst.png) center center no-repeat;
  }
  &__viber{
    background: url(../img/footer__viber.png) center center no-repeat;
    background-size: contain;
  }
  @media screen and (max-width: 380px) {
    &__bottom{
      flex-wrap: wrap;
      p, .footer__socials{
        flex-basis: 100%;
      }
      p{
        margin-bottom: 15px;
      }
    }
  }
}