@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;800&family=Roboto+Slab:wght@400;600;800&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  line-height: 120%;
}

.container {
  max-width: 1120px;
  padding: 0 10px;
  margin: 0 auto;
}

.header {
  position: relative;
  width: 100%;
  margin: 0 auto 25px auto;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 5px solid #dfdfdf;
  background: url(../img/bg.png);
}
.header .burger-btn {
  z-index: 10;
  position: absolute;
  width: 40px;
  height: 30px;
  top: 25px;
  left: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #000000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 14px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 27px;
}
.header .burger-btn:after {
  bottom: 27px;
}
.header .burger-btn_active span {
  opacity: 0;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 13px;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 14px;
}
.header__menu {
  display: flex;
  flex-direction: column;
  z-index: 8;
  position: absolute;
  width: 100%;
  left: 0;
  top: -300px;
  background-color: #F2F2F2;
  background-image: url(../img/header__img.png);
  background-position-x: right;
  background-position-y: center;
  background-repeat: no-repeat;
  padding: 66px 0 35px 23px;
  transition: all ease 0.5s;
}
.header__menu_active {
  top: 0;
}
.header__menu a {
  max-width: fit-content;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  background: #000000;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 15px;
  transition: all ease 0.5s;
}
.header__menu a:last-child {
  margin-bottom: 0;
}
.header__menu a:hover {
  background-image: linear-gradient(90deg, #E3535D, #2AA5A0);
}
.header__logo {
  display: flex;
  max-width: 180px;
  width: 100%;
  margin: 0 auto;
}
.header__logo img {
  margin-right: 15px;
}
.header__logo p {
  font-family: "Roboto Slab", serif;
  font-size: 12px;
  font-weight: 600;
  color: #000000;
}
.header__logo p b {
  font-weight: 800;
}

.news {
  width: 100%;
  padding: 12px 15px 17px 15px;
  margin-bottom: 25px;
  background: #fff;
  box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
}
.news:last-child {
  margin-bottom: 35px;
}
.news__title {
  font-family: "Roboto Slab", serif;
  font-size: 24px;
  font-weight: 600;
  color: #000000;
  margin-bottom: 5px;
}
.news__text {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  margin-bottom: 10px;
}
.news__link {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #4BCF0D;
}
.news__sliders {
  margin-bottom: 25px;
}

.popup-slider {
  width: 100%;
  margin-bottom: 10px;
}
.popup-slider__prev, .popup-slider__next {
  position: absolute;
  top: 110px;
  z-index: 5;
  opacity: 0.8;
  cursor: pointer;
}
.popup-slider__prev {
  left: 20px;
}
.popup-slider__next {
  right: 20px;
}
.popup-slider__slide {
  height: 240px !important;
}
.popup-slider__slide img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
@media screen and (min-width: 769px) {
  .popup-slider__slide img {
    object-fit: contain;
  }
}

.popup-thumbs {
  height: 50px;
  box-sizing: border-box;
  padding: 10px 0;
  width: 100%;
}
.popup-thumbs .swiper-slide {
  height: 100%;
  max-width: 90px;
  padding: 1.5px;
  opacity: 0.5;
}
.popup-thumbs .swiper-slide-thumb-active {
  opacity: 1;
  border: 2px solid #33A018;
}
.popup-thumbs img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.write-us, .our-contact {
  display: block;
  margin: 0 auto 17px auto;
  max-width: 250px;
  font-family: "Open Sans", sans-serif;
  font-size: 24px;
  font-weight: 400;
  color: #F12929;
  padding: 7px 40px;
  border: 2px solid #F12929;
  border-radius: 100px;
}

.our-contact {
  color: #33A018;
  border-color: #33A018;
  padding: 7px 30px;
  margin-bottom: 90px;
}

.footer {
  position: relative;
  padding: 20px 0 40px 0;
  background-color: #DFDFDF;
  background-image: url(../img/footer__img.png);
  background-position-x: left;
  background-position-y: center;
  background-repeat: no-repeat;
}
.footer__wrapper {
  max-width: 1120px;
  padding: 0 20px;
  margin: 0 auto;
}
.footer__text {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #535353;
  margin-bottom: 25px;
}
.footer__text b {
  font-weight: 800;
}
.footer__line {
  width: 100%;
  height: 2px;
  background: #535353;
  margin-bottom: 20px;
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__bottom p {
  font-weight: 600;
  margin-bottom: 0;
}
.footer__socials {
  max-height: 25px;
  width: 205px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__link {
  width: 25px;
  height: 25px;
}
.footer__telegram {
  background: url(../img/footer__telegram.png) center center no-repeat;
}
.footer__facebook {
  background: url(../img/footer__facebook.png) center center no-repeat;
}
.footer__inst {
  background: url(../img/footer__inst.png) center center no-repeat;
}
.footer__viber {
  background: url(../img/footer__viber.png) center center no-repeat;
  background-size: contain;
}
@media screen and (max-width: 380px) {
  .footer__bottom {
    flex-wrap: wrap;
  }
  .footer__bottom p, .footer__bottom .footer__socials {
    flex-basis: 100%;
  }
  .footer__bottom p {
    margin-bottom: 15px;
  }
}